<template>
  <div class="container">
    <h1>绵羊墙</h1>
    <p style="width: 800px;text-align:left; font-size: 16px">“绵羊墙” The Wall of Sheep
一面展示板，将用户设置的不安全的网络用户名与密码公布在上。
绵羊墙是在西方举行的各种黑客大会或安全大会上经常出现的趣味活动，源自于黑客大会的鼻祖Defcon。</p>
    <el-table
    :data="tableData"
    style="width: 80%"
    border
    height="540"
    >
      <el-table-column
        prop="qq"
        label="头像"
        width="400">
        <template scope="scope">
          <img :src="'https://q.qlogo.cn/headimg_dl?dst_uin='+ scope.row.qq +'&spec=100'">
        </template>
      </el-table-column>
      <el-table-column
        prop="qq"
        label="QQ号"
        >
        <template scope="scope">
          <p>{{scope.row.qq}}</p>
        </template>
      </el-table-column>
    </el-table>
    <div class="vue"><i>*</i>此组件前端框架为vue2.0 ,推爆</div>
  </div>
</template>

<script>
  import axios from 'axios';
  export default {
    data () {
      return {
        tableData: [],
      }
    },  
    methods: {
      getInfo () {
        axios.get('http://192.168.43.179:8881/').then((res) => {
          this.tableData = res.data.data;
        })
      }
    },
    mounted() {
      setInterval(() => {
        this.getInfo();
      }, 1000)
    },
    updated() {
      this.$nextTick(() => {
        var container = this.$el.querySelector(".el-table__body-wrapper.is-scrolling-none");
        container.scrollTop = container.scrollHeight;
      })
   }
  }
</script>

<style scoped>
.container {
  /* color: white; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.vue {
  height: 20px;
  width: 100%;
  position: absolute;
  bottom: 10px;
  left: 0;
  color: gray;
  font-style: italic;
  font-weight: lighter;
}
i {
  color: red;
}
h1 {
  font-size: 60px;
  margin: 10px;
}
img {
  height: 70px;
  width: 70px;
}
p {
  font-size: 24px;
}
</style>


